<style scoped>
.navContainer {
    width: 100%;
    height: 80px;
    overflow: hidden;
    /* 导航栏阴影动画 */
    transition: all .4s ease;

    background: rgba(255, 255, 255, 0.95);
    box-shadow: 0 1px 40px -8px rgba(0, 0, 0, .5);

    /* 水平左对齐 */
    display: flex;
    justify-content: flex-start;

    /* 竖直剧中 */
    align-items: center;

    .navLeft {
        display: flex;
        justify-self: flex-start;
        align-items: center;

        .navLeftBox {
            /* 左间距 */
            margin-left: 30px;
            color: #666666;
            text-decoration: none;

            /* 超链接下划线动画 */
            .navItem::after {
                content: "";
                display: block;
                position: absolute;
                height: 3px;
                background-color: #0099FF;
                width: 0;
                transition: width .25s ease-in-out;
            }

            &:hover .navItem {
                color: black;
            }

            &:hover .navItem:after {
                width: 30px;
            }
        }

    }


    .navRight {
        position: absolute;
        display: flex;
        right: 70px;
    }

    .minRight {
        position: relative;
        left: 90%;
        width: 32px;
        height: 32px;
        display: none;
    }

    @media (max-width:960px) {
        .logo {
            position: absolute;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        /* 隐藏 */
        .navLeft {
            display: none;
        }

        .navRight {
            display: none;
        }

        /* 显示 */
        .minRight {
            display: block;
        }
    }

}

@media (max-width: 960px) {

    /* LOGO剧中 */
    .navContainer {
        position: relative;
    }

    .svgSetting{
        display: none;
    }

    .loginbutton{
        display: none;
    }
}

/* 设置按钮 */
.svgSetting {
    width: 32px;
    height: 32px;
    animation: rotateAnimation 2s infinite linear;
}

/* 设置旋转关键帧 */
@keyframes rotateAnimation {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}
</style>

<script setup>
import { ref } from 'vue'
import Logo from './components/NavLogo.vue'
import ModelDialog from './components/Login/ModelDialog.vue';
import Login from './components/Login/LoginDialog.vue';
import NavSetting from './components/NavSetting.vue';
const LoginVisible = ref(false)
const SettingVisible = ref(false)

</script>